import React, { Component} from "react"
import Producto from './Producto.js'


import {

  Card,
  Button,
  Modal,
  FormGroup,
  Row,
  Col,
  Form
} from "react-bootstrap";

class Seccion extends Component{
  constructor(){
    super();
    this.state={

    modalActualizar: false,
    mostrarProductos: false}
  this.handleInput=this.handleInput.bind(this)
  this.handleSubmit=this.handleSubmit.bind(this)
  this.setModal=this.setModal.bind(this)
}  
    handleInput(e){
    e.preventDefault()
    const {value, name} =e.target 
    if(value!==""){
    this.setState({
      [name]: value})}}
    handleSubmit(id){
      const idp=Math.floor(Math.random() * 10000) + 1
      const nuevoproducto={idp:idp,nombreProducto: this.state.nombreProducto, descripcion: this.state.descripcion, precio:this.state.precio, grupoOpciones:[]}
      this.props.add("producto",nuevoproducto,id)}  
      setModal(producto){
        this.setState({modalActualizar:true,nombreSeccion:producto.nombreSeccion})}
    render(){
return (
  <Card>
  <Card.Header>
    <div>
    <Row>
  <Col><h4 className="col">{this.props.seccion.nombreSeccion}</h4></Col>               
  <Col style={{textAlign: "rigth"}}>
  <Button variant="primary" onClick={() => this.setModal(this.props.seccion)}>Editar</Button> {' '}
  <Button  variant="warning"  onClick={this.props.copy.bind(this,"seccion",this.props.seccion)}>Copiar</Button>{' '}
  <Button  variant="danger" onClick={this.props.remove.bind(this,"seccion",this.props.seccion.ids)}>Borrar</Button> {' '}</Col> 
  <Button size="sm" variant="light" onClick={() => this.setState({mostrarProductos:!this.state.mostrarProductos})}>{this.state.mostrarProductos? "Ocultar Productos": "Mostrar Productos"}</Button> 
  </Row>
  </div>
  </Card.Header>
  <Modal show={this.state.modalActualizar} >
  <Modal.Header>
   <div><h3>Cambiar Sección</h3></div>
  </Modal.Header>
  <Modal.Body >
    <FormGroup>
      <label>
        Nombre seccion: 
      </label>
      <input
        className="form-control"
        name="nombreSeccion"
        type="text"
        onChange={this.handleInput}
        placeholder={this.props.seccion.nombreSeccion}
        defaultValue={this.props.seccion.nombreSeccion}
      />
    </FormGroup>
  </Modal.Body>
  <Modal.Footer>
    <Button
      variant="success"
      onClick={() => {this.props.edit("seccion",this.state.nombreSeccion,this.props.seccion.ids);this.setState({modalActualizar:false})}}
    >
      Editar
    </Button>
    <Button
      variant="danger"
      onClick={() => this.setState({modalActualizar:false})}
    >
      Cancelar
    </Button>
  </Modal.Footer>
</Modal>       

  {this.state.mostrarProductos &&
(<Card.Body>
   {this.props.seccion.productos.map((producto)=> {return <Producto  key={producto.idp} producto={producto} edit={this.props.edit} 
    remove={this.props.remove} ids={this.props.seccion.ids} add={this.props.add} copy={this.props.copy} paste={this.props.paste}/>})}
  
<Form.Group style={{padding: "1rem"}}>
<Row  >
<Col><input type="text" required onChange={this.handleInput} name="nombreProducto" className="form-control" placeholder="Nombre de Producto"></input></Col>
<Col><input type="number" required min="0" onChange={this.handleInput} name="precio" className="form-control" placeholder="Precio"></input></Col>
</Row>
<Row style={{padding: "1rem"}} >
<input type="text" required onChange={this.handleInput} name="descripcion" className="form-control" placeholder="Descripción de Producto"></input>
</Row>
<Button type="button"  variant="success" onClick={this.handleSubmit.bind(this,this.props.seccion.ids)}>Agregar Nuevo Producto</Button> 
<button  type="button" className="btn btn-warning" onClick={this.props.paste.bind(this,"producto",this.props.seccion.ids)}>Pegar Producto</button>
</Form.Group>
</Card.Body> )}
    
 
    </Card>
   
  )     


}}
export default Seccion;